import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
import './editor.scss';

export default function Edit() {
	return (
		<div { ...useBlockProps() }>
			<InnerBlocks
				allowedBlocks={ [ 'blocks-mk/team-member' ] }
				template={ [
					[ 'blocks-mk/team-member' ],
					[ 'blocks-mk/team-member' ],
					[ 'blocks-mk/team-member' ],
				] }
			/>
		</div>
	);
}
